import popUP from '../../components/pop-up/pop-up.vue'
import util from "../../common/js/util";
export default{
	data(){
		return{
			UserList:[
			{
				Id:'1',
				name:'asdas',
				sex:'1',
				phone:'123123112',
				age:'14',
				addr:'1312',
				birth:'2018-11-29',
				remark:'老汉'
			}
			],
			sendVal:false,
			dendVal:false,
			dialogStatus: "",
			textMap: {
				update: "编辑",
				create: "添加"
			},
			dialogFormVisible: false,
			editForm: {
				id: "0",
				name: "",
				sex: -1,
				age: 0,
				phone:"",
				birth: "",
				remark:"",
				addr: ""
			},
			sels: [] //列表选中列
		}
	},
	created(){
	},
	methods:{
		//显示编辑
		openMask(index,row){
			/*this.sendVal=true;*/
			this.dialogStatus = "update";
			this.dialogFormVisible = true;
			this.editForm = Object.assign({}, row);
		},
		//删除页面
		DeleteMask(){
			this.dendVal=true;
			this.$confirm("确认删除该记录吗?", "提示", {
				type: "warning"
			})
			.then(() => {
				this.UserList.splice(index,1);
				this.$message({
					message: "删除成功",
					type: "success"
				});
			})
			.catch(() => {});
		},
		//批量删除
		batchRemove: function() {
			var ids = this.sels.map(item => item.id).toString();
			this.$confirm("确认删除选中记录吗？", "提示", {
				type: "warning"
			})
			.then(() => {
         // this.listLoading = true;
          //NProgress.start();
          let para = { ids: ids };
          batchRemoveUser(para).then(res => {
            //this.listLoading = false;
            //NProgress.done();
            this.UserList.split(index,ids)
            this.$message({
            	message: "删除成功",
            	type: "success"
            	/*});*/
            });
        })
          .catch(() => {});
      })
		},
		//编辑页面
		updateData:function() {
			this.$refs.editForm.validate(valid => {
				if (valid) {
					this.$confirm("确认提交吗？", "提示", {})
					.then(() => {
		              //this.editLoading = true;
		              //NProgress.start();
		              let para = Object.assign({}, this.editForm);
		              para.birth =
		              !para.birth || para.birth == ""
		              ? ""
		              : util.formatDate.format(new Date(para.birth), "yyyy-MM-dd");
		              
		              /*this.UserList(para).then(res => {*/
		                //this.editLoading = false;
		                //NProgress.done();
		                this.$message({
		                	message: "提交成功",
		                	type: "success"
		                });
		                this.$refs["editForm"].resetFields();
		                //this.editFormVisible = false;
		                this.dialogFormVisible = false;
		                /*});*/
		            })
					.catch(e => {
			              // 打印一下错误
			              console.log(e);
			          });
				}
			});
		},
		//全选单选多选
		selsChange: function(sels) {
			this.sels = sels;
		},
		//性别显示转换
		formatSex: function(row, column) {
			return row.sex == 1 ? "男" : row.sex == 0 ? "女" : "未知";
		},
		//显示新增界面
		handleAdd: function() {
			this.dialogStatus = "create";
	      //this.addFormVisible = true;
	      this.dialogFormVisible = true;
	      this.editForm = {
	      	id: "0",
	      	name: "",
	      	sex: -1,
	      	age: 0,
	      	phone:"",
	      	birth: "",
	      	remark:"",
	      	addr: ""
	      }
	  },
	  //新增
	  createData: function() {
	  	this.$refs.editForm.validate(valid => {
	  		if (valid) {
	  			this.$confirm("确认提交吗？", "提示", {})
	  			.then(() => {
              //this.editLoading = true;
              //NProgress.start();
              this.editForm.id = (parseInt(Math.random() * 100)).toString() // mock a id
              let para = Object.assign({}, this.editForm);
              console.log(para);
              
              para.birth =
              !para.birth || para.birth == ""
              ? ""
              : util.formatDate.format(new Date(para.birth), "yyyy-MM-dd");
              addUser(para).then(res => {
                //this.addLoading = false;
                //NProgress.done();
                this.$message({
                	message: "提交成功",
                	type: "success"
                });
                this.$refs["editForm"].resetFields();
                this.dialogFormVisible = false;
                //this.addFormVisible = false;
                this.getUsers();
            });
          })
	  			.catch(e => {
              // 打印一下错误
              console.log(e);
          });
	  		}
	  	});
	  }
	},
	components:{
		'pop-up':popUP
	}
}


